import React, { useState, useEffect } from 'react';
import {
  View,
  Text,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  Alert,
  ActivityIndicator,
  Image,
} from 'react-native';
import { useTheme } from '../theme/ThemeContext';
import Header from '../components/Header';
import FavoriteButton from '../components/FavoriteButton';
import { getRecipeById, toggleFavorite } from '../storage/recipeStorage';
import { exportJson } from '../utils/exportJson';

const RecipeDetailsScreen = ({ route, navigation }) => {
  const { recipeId } = route.params;
  const { colors } = useTheme();
  const [recipe, setRecipe] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadRecipe();
  }, [recipeId]);

  const loadRecipe = async () => {
    setLoading(true);
    const data = await getRecipeById(recipeId);
    setRecipe(data);
    setLoading(false);
  };

  const handleToggleFavorite = async () => {
    await toggleFavorite(recipeId);
    setRecipe((prev) => (prev ? { ...prev, isFavorite: !prev.isFavorite } : null));
  };

  const handleExport = async () => {
    if (!recipe) return;
    const sanitized = recipe.title
      .replace(/[^a-zA-Z0-9\u0590-\u05FF\s]/g, '')
      .replace(/\s+/g, '-')
      .substring(0, 50);
    const filename = `${sanitized || 'recipe'}.json`;
    const ok = await exportJson(recipe, filename);
    if (!ok) Alert.alert('שגיאה', 'שגיאה בייצוא המתכון');
  };

  if (loading) {
    return (
      <View style={[styles.loadingWrap, { backgroundColor: colors.background }]}>
        <ActivityIndicator size="large" color={colors.primary} />
      </View>
    );
  }

  if (!recipe) {
    return (
      <View style={[styles.container, { backgroundColor: colors.background }]}>
        <Header title="פרטי המתכון" onBack={() => navigation.goBack()} />
        <View style={styles.errorWrap}>
          <Text style={[styles.errorText, { color: colors.textSecondary }]}>
            המתכון לא נמצא
          </Text>
        </View>
      </View>
    );
  }

  const hasImage = recipe.imageUri && recipe.imageUri.length > 0;

  return (
    <View style={[styles.container, { backgroundColor: colors.background }]}>
      <Header title="פרטי המתכון" onBack={() => navigation.goBack()} />

      <ScrollView
        style={styles.scroll}
        contentContainerStyle={styles.scrollContent}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.hero}>
          {hasImage ? (
            <Image
              source={{ uri: recipe.imageUri }}
              style={styles.heroImage}
              resizeMode="cover"
            />
          ) : (
            <View style={[styles.heroPlaceholder, { backgroundColor: colors.primary }]}>
              <Text style={styles.heroPlaceholderText}>מתכון</Text>
            </View>
          )}
        </View>

        <View style={styles.content}>
          <View style={styles.titleRow}>
            <Text style={[styles.title, { color: colors.text }]}>{recipe.title}</Text>
            <FavoriteButton
              isFavorite={recipe.isFavorite}
              onPress={handleToggleFavorite}
              size={32}
            />
          </View>
          {recipe.category ? (
            <View style={[styles.categoryChip, { backgroundColor: colors.primary }]}>
              <Text style={styles.categoryText}>{recipe.category}</Text>
            </View>
          ) : null}

          {recipe.ingredients ? (
            <View style={styles.section}>
              <Text style={[styles.sectionLabel, { color: colors.primary }]}>
                מצרכים
              </Text>
              <View style={[styles.textBox, { backgroundColor: colors.surface }]}>
                <Text style={[styles.sectionText, { color: colors.text }]}>
                  {recipe.ingredients}
                </Text>
              </View>
            </View>
          ) : null}

          {recipe.instructions ? (
            <View style={styles.section}>
              <Text style={[styles.sectionLabel, { color: colors.primary }]}>
                אופן הכנה
              </Text>
              <View style={[styles.textBox, { backgroundColor: colors.surface }]}>
                <Text style={[styles.sectionText, { color: colors.text }]}>
                  {recipe.instructions}
                </Text>
              </View>
            </View>
          ) : null}

          {recipe.notes ? (
            <View style={styles.section}>
              <Text style={[styles.sectionLabel, { color: colors.primary }]}>
                הערות
              </Text>
              <View style={[styles.textBox, { backgroundColor: colors.surface }]}>
                <Text style={[styles.sectionText, { color: colors.text }]}>
                  {recipe.notes}
                </Text>
              </View>
            </View>
          ) : null}
        </View>
      </ScrollView>

      <View style={[styles.footer, { backgroundColor: colors.background, borderTopColor: colors.border }]}>
        <TouchableOpacity
          style={[styles.footerBtn, { backgroundColor: colors.primary }]}
          onPress={() => navigation.navigate('AddEditRecipe', { recipeId: recipe.id })}
        >
          <Text style={styles.footerBtnText}>ערוך</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={[styles.footerBtn, styles.footerBtnSecondary, { borderColor: colors.primary }]}
          onPress={handleExport}
        >
          <Text style={[styles.footerBtnTextSecondary, { color: colors.primary }]}>
            ייצא מתכון
          </Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  loadingWrap: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  scroll: {
    flex: 1,
  },
  scrollContent: {
    paddingBottom: 120,
  },
  hero: {
    height: 220,
    width: '100%',
  },
  heroImage: {
    width: '100%',
    height: '100%',
  },
  heroPlaceholder: {
    width: '100%',
    height: '100%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  heroPlaceholderText: {
    fontSize: 24,
    color: '#FFFFFF',
    fontWeight: '600',
  },
  content: {
    padding: 20,
  },
  titleRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginBottom: 12,
  },
  title: {
    fontSize: 28,
    fontWeight: 'bold',
    textAlign: 'right',
    flex: 1,
    marginLeft: 12,
  },
  categoryChip: {
    alignSelf: 'flex-end',
    paddingHorizontal: 16,
    paddingVertical: 8,
    borderRadius: 12,
    marginBottom: 24,
  },
  categoryText: {
    fontSize: 14,
    fontWeight: '600',
    color: '#FFFFFF',
    textAlign: 'right',
  },
  section: {
    marginBottom: 24,
  },
  sectionLabel: {
    fontSize: 18,
    fontWeight: 'bold',
    textAlign: 'right',
    marginBottom: 10,
  },
  textBox: {
    borderRadius: 16,
    padding: 16,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 1 },
    shadowOpacity: 0.05,
    shadowRadius: 2,
    elevation: 2,
  },
  sectionText: {
    fontSize: 16,
    textAlign: 'right',
    lineHeight: 24,
  },
  errorWrap: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
  },
  errorText: {
    fontSize: 18,
    textAlign: 'center',
  },
  footer: {
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    flexDirection: 'row',
    paddingHorizontal: 20,
    paddingVertical: 16,
    paddingBottom: 34,
    borderTopWidth: 1,
    gap: 12,
  },
  footerBtn: {
    flex: 1,
    borderRadius: 12,
    paddingVertical: 14,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 4,
  },
  footerBtnSecondary: {
    backgroundColor: 'transparent',
    borderWidth: 2,
  },
  footerBtnText: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#FFFFFF',
    textAlign: 'center',
  },
  footerBtnTextSecondary: {
    fontSize: 16,
    fontWeight: 'bold',
    textAlign: 'center',
  },
});

export default RecipeDetailsScreen;
