import React from 'react';
import { ScrollView, TouchableOpacity, Text, StyleSheet } from 'react-native';
import { useTheme } from '../theme/ThemeContext';

/**
 * Horizontal scrollable category chips. Selected chip is highlighted.
 * Pass existing categories + optional newCategoryLabel for "add new" chip.
 */
const CategoryChips = ({
  categories,
  selectedCategory,
  onSelectCategory,
  newCategoryLabel = '+ קטגוריה חדשה',
  onAddNew,
}) => {
  const { colors } = useTheme();

  return (
    <ScrollView
      horizontal
      showsHorizontalScrollIndicator={false}
      contentContainerStyle={styles.scrollContent}
      style={styles.scroll}
    >
      {categories.map((cat) => {
        const isSelected = selectedCategory === cat;
        return (
          <TouchableOpacity
            key={cat}
            style={[
              styles.chip,
              {
                backgroundColor: isSelected ? colors.primary : colors.surface,
                borderColor: colors.border,
              },
            ]}
            onPress={() => onSelectCategory(cat)}
          >
            <Text
              style={[
                styles.chipText,
                { color: isSelected ? '#FFFFFF' : colors.text },
              ]}
              numberOfLines={1}
            >
              {cat}
            </Text>
          </TouchableOpacity>
        );
      })}
      {onAddNew && (
        <TouchableOpacity
          style={[styles.chip, styles.newChip, { borderColor: colors.primary }]}
          onPress={onAddNew}
        >
          <Text style={[styles.chipText, { color: colors.primary }]}>
            {newCategoryLabel}
          </Text>
        </TouchableOpacity>
      )}
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  scroll: {
    flexGrow: 0,
  },
  scrollContent: {
    paddingVertical: 8,
    paddingHorizontal: 4,
    gap: 8,
    flexDirection: 'row',
    alignItems: 'center',
  },
  chip: {
    paddingHorizontal: 16,
    paddingVertical: 10,
    borderRadius: 20,
    borderWidth: 1,
    marginHorizontal: 4,
  },
  newChip: {
    backgroundColor: 'transparent',
    borderStyle: 'dashed',
  },
  chipText: {
    fontSize: 14,
    fontWeight: '600',
  },
});

export default CategoryChips;
