import React from 'react';
import {
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  Image,
} from 'react-native';
import { useTheme } from '../theme/ThemeContext';
import FavoriteButton from './FavoriteButton';

/**
 * Recipe card with image, title, category chip, favorite, ingredients preview, delete.
 */
const RecipeCard = ({ recipe, onPress, onDelete, onToggleFavorite }) => {
  const { colors } = useTheme();

  const getIngredientsPreview = (ingredients) => {
    if (!ingredients) return '';
    const lines = ingredients.split('\n').filter((line) => line.trim());
    return lines.slice(0, 2).join('\n');
  };

  const hasImage = recipe.imageUri && recipe.imageUri.length > 0;

  return (
    <TouchableOpacity
      style={[styles.card, { backgroundColor: colors.surface }]}
      onPress={onPress}
      activeOpacity={0.85}
    >
      <View style={styles.imageWrap}>
        {hasImage ? (
          <Image
            source={{ uri: recipe.imageUri }}
            style={styles.image}
            resizeMode="cover"
          />
        ) : (
          <View style={[styles.placeholder, { backgroundColor: colors.primary }]}>
            <Text style={styles.placeholderText}>מתכון</Text>
          </View>
        )}
        <View style={styles.topActions}>
          {recipe.category ? (
            <View style={[styles.categoryChip, { backgroundColor: colors.primary }]}>
              <Text style={styles.categoryText} numberOfLines={1}>
                {recipe.category}
              </Text>
            </View>
          ) : null}
          <TouchableOpacity
            style={styles.deleteBtn}
            onPress={(e) => {
              e.stopPropagation();
              onDelete();
            }}
          >
            <Text style={styles.deleteText}>×</Text>
          </TouchableOpacity>
        </View>
        <View style={styles.favoriteWrap}>
          <FavoriteButton
            isFavorite={recipe.isFavorite}
            onPress={(e) => {
              e?.stopPropagation?.();
              onToggleFavorite();
            }}
            size={24}
          />
        </View>
      </View>
      <View style={styles.body}>
        <Text style={[styles.title, { color: colors.text }]} numberOfLines={1}>
          {recipe.title}
        </Text>
        {recipe.ingredients ? (
          <Text
            style={[styles.preview, { color: colors.textSecondary }]}
            numberOfLines={2}
          >
            {getIngredientsPreview(recipe.ingredients)}
          </Text>
        ) : null}
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  card: {
    borderRadius: 16,
    marginHorizontal: 16,
    marginVertical: 8,
    overflow: 'hidden',
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
  imageWrap: {
    height: 160,
    position: 'relative',
  },
  image: {
    width: '100%',
    height: '100%',
  },
  placeholder: {
    width: '100%',
    height: '100%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  placeholderText: {
    fontSize: 18,
    color: '#FFFFFF',
    fontWeight: '600',
  },
  topActions: {
    position: 'absolute',
    top: 12,
    left: 12,
    right: 12,
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  categoryChip: {
    paddingHorizontal: 12,
    paddingVertical: 6,
    borderRadius: 12,
    maxWidth: '60%',
  },
  categoryText: {
    fontSize: 12,
    fontWeight: '600',
    color: '#FFFFFF',
    textAlign: 'right',
  },
  deleteBtn: {
    width: 32,
    height: 32,
    borderRadius: 16,
    backgroundColor: 'rgba(0,0,0,0.4)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  deleteText: {
    fontSize: 22,
    color: '#FFFFFF',
    fontWeight: 'bold',
    lineHeight: 22,
  },
  favoriteWrap: {
    position: 'absolute',
    bottom: 12,
    left: 12,
  },
  body: {
    padding: 16,
  },
  title: {
    fontSize: 20,
    fontWeight: 'bold',
    marginBottom: 6,
    textAlign: 'right',
  },
  preview: {
    fontSize: 14,
    lineHeight: 20,
    textAlign: 'right',
  },
});

export default RecipeCard;
