import React, { useState, useCallback, useMemo } from 'react';
import {
  View,
  Text,
  FlatList,
  TextInput,
  StyleSheet,
  TouchableOpacity,
  Alert,
} from 'react-native';
import { useFocusEffect } from '@react-navigation/native';
import { useTheme } from '../theme/ThemeContext';
import RecipeCard from '../components/RecipeCard';
import Header from '../components/Header';
import CategoryChips from '../components/CategoryChips';
import {
  getRecipes,
  deleteRecipe,
  toggleFavorite,
} from '../storage/recipeStorage';
import { exportJson } from '../utils/exportJson';

const HomeScreen = ({ navigation }) => {
  const { colors, toggleTheme } = useTheme();
  const [recipes, setRecipes] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [showFavoritesOnly, setShowFavoritesOnly] = useState(false);

  useFocusEffect(
    useCallback(() => {
      loadRecipes();
    }, [])
  );

  const loadRecipes = async () => {
    const list = await getRecipes();
    const sorted = list.sort((a, b) => (b.createdAt || 0) - (a.createdAt || 0));
    setRecipes(sorted);
  };

  const categories = useMemo(() => {
    const set = new Set();
    recipes.forEach((r) => r.category && set.add(r.category));
    return Array.from(set).sort();
  }, [recipes]);

  const filteredRecipes = useMemo(() => {
    let list = recipes;
    if (showFavoritesOnly) {
      list = list.filter((r) => r.isFavorite);
    }
    if (selectedCategory) {
      list = list.filter((r) => r.category === selectedCategory);
    }
    if (searchQuery.trim()) {
      const q = searchQuery.toLowerCase();
      list = list.filter((r) => r.title.toLowerCase().includes(q));
    }
    return list;
  }, [recipes, searchQuery, selectedCategory, showFavoritesOnly]);

  const handleDelete = (recipe) => {
    Alert.alert(
      'מחיקת מתכון',
      `האם אתה בטוח שברצונך למחוק את "${recipe.title}"?`,
      [
        { text: 'ביטול', style: 'cancel' },
        {
          text: 'מחק',
          style: 'destructive',
          onPress: async () => {
            await deleteRecipe(recipe.id);
            loadRecipes();
          },
        },
      ]
    );
  };

  const handleToggleFavorite = async (recipe) => {
    await toggleFavorite(recipe.id);
    loadRecipes();
  };

  const handleExportAll = async () => {
    const all = await getRecipes();
    const ok = await exportJson(all, 'recipes-backup.json');
    if (!ok) Alert.alert('שגיאה', 'שגיאה בייצוא המתכונים');
  };

  return (
    <View style={[styles.container, { backgroundColor: colors.background }]}>
      <Header
        title="המתכונים שלי"
        rightComponent={
          <View style={styles.headerActions}>
            <TouchableOpacity onPress={handleExportAll} style={styles.headerBtn}>
              <Text style={[styles.headerBtnText, { color: colors.primary }]}>ייצא הכל</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={toggleTheme} style={styles.themeBtn}>
              <Text style={styles.themeBtnText}>{colors.background === '#FDF6EC' ? '🌙' : '☀️'}</Text>
            </TouchableOpacity>
          </View>
        }
      />
      <View style={styles.searchRow}>
        <TextInput
          style={[
            styles.searchInput,
            {
              backgroundColor: colors.surface,
              color: colors.text,
              borderColor: colors.border,
            },
          ]}
          placeholder="חפש מתכון..."
          placeholderTextColor={colors.textMuted}
          value={searchQuery}
          onChangeText={setSearchQuery}
        />
      </View>
      <View style={styles.toggleRow}>
        <TouchableOpacity
          style={[
            styles.toggleBtn,
            {
              backgroundColor: !showFavoritesOnly ? colors.primary : colors.surface,
              borderColor: !showFavoritesOnly ? colors.primary : colors.border,
            },
          ]}
          onPress={() => setShowFavoritesOnly(false)}
        >
          <Text
            style={[
              styles.toggleText,
              { color: !showFavoritesOnly ? '#FFFFFF' : colors.text },
            ]}
          >
            הכל
          </Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={[
            styles.toggleBtn,
            {
              backgroundColor: showFavoritesOnly ? colors.primary : colors.surface,
              borderColor: showFavoritesOnly ? colors.primary : colors.border,
            },
          ]}
          onPress={() => setShowFavoritesOnly(true)}
        >
          <Text
            style={[
              styles.toggleText,
              { color: showFavoritesOnly ? '#FFFFFF' : colors.text },
            ]}
          >
            מועדפים
          </Text>
        </TouchableOpacity>
      </View>
      <CategoryChips
        categories={['הכל', ...categories]}
        selectedCategory={selectedCategory === null ? 'הכל' : selectedCategory}
        onSelectCategory={(cat) => setSelectedCategory(cat === 'הכל' ? null : cat)}
      />
      <FlatList
        data={filteredRecipes}
        keyExtractor={(item) => item.id}
        contentContainerStyle={styles.listContent}
        ListEmptyComponent={
          <View style={styles.empty}>
            <Text style={[styles.emptyText, { color: colors.textSecondary }]}>
              {searchQuery || selectedCategory || showFavoritesOnly
                ? 'לא נמצאו מתכונים'
                : 'אין מתכונים עדיין'}
            </Text>
            <Text style={[styles.emptySub, { color: colors.textMuted }]}>
              {searchQuery || selectedCategory || showFavoritesOnly
                ? 'נסה לשנות סינון או חיפוש'
                : 'לחץ על + כדי להוסיף מתכון'}
            </Text>
          </View>
        }
        renderItem={({ item }) => (
          <RecipeCard
            recipe={item}
            onPress={() => navigation.navigate('RecipeDetails', { recipeId: item.id })}
            onDelete={() => handleDelete(item)}
            onToggleFavorite={() => handleToggleFavorite(item)}
          />
        )}
      />
      <TouchableOpacity
        style={[styles.fab, { backgroundColor: colors.primary }]}
        onPress={() => navigation.navigate('AddEditRecipe', {})}
      >
        <Text style={styles.fabText}>+</Text>
      </TouchableOpacity>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  searchRow: {
    paddingHorizontal: 16,
    paddingVertical: 12,
  },
  searchInput: {
    borderRadius: 12,
    paddingHorizontal: 16,
    paddingVertical: 12,
    fontSize: 16,
    textAlign: 'right',
    borderWidth: 1,
  },
  toggleRow: {
    flexDirection: 'row',
    paddingHorizontal: 16,
    paddingBottom: 8,
    gap: 12,
    justifyContent: 'flex-end',
  },
  toggleBtn: {
    paddingHorizontal: 20,
    paddingVertical: 10,
    borderRadius: 20,
    borderWidth: 1,
  },
  toggleText: {
    fontSize: 14,
    fontWeight: '600',
  },
  listContent: {
    paddingBottom: 100,
  },
  empty: {
    paddingTop: 80,
    paddingHorizontal: 24,
    alignItems: 'center',
  },
  emptyText: {
    fontSize: 18,
    fontWeight: '600',
    marginBottom: 8,
    textAlign: 'center',
  },
  emptySub: {
    fontSize: 14,
    textAlign: 'center',
  },
  fab: {
    position: 'absolute',
    right: 20,
    bottom: 24,
    width: 64,
    height: 64,
    borderRadius: 32,
    justifyContent: 'center',
    alignItems: 'center',
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.3,
    shadowRadius: 6,
    elevation: 8,
  },
  fabText: {
    fontSize: 36,
    color: '#FFFFFF',
    fontWeight: '300',
    lineHeight: 36,
  },
  headerActions: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 8,
  },
  headerBtn: {
    padding: 6,
  },
  headerBtnText: {
    fontSize: 14,
    fontWeight: '600',
  },
  themeBtn: {
    padding: 8,
  },
  themeBtnText: {
    fontSize: 22,
  },
});

export default HomeScreen;
