import React, { useState, useEffect } from 'react';
import {
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  ScrollView,
  Alert,
  KeyboardAvoidingView,
  Platform,
  Image,
} from 'react-native';
import { v4 as uuidv4 } from 'uuid';
import { pickImage } from '../utils/pickImage';
import { useTheme } from '../theme/ThemeContext';
import Header from '../components/Header';
import CustomInput from '../components/CustomInput';
import CategoryChips from '../components/CategoryChips';
import {
  getRecipes,
  getRecipeById,
  addRecipe,
  updateRecipe,
} from '../storage/recipeStorage';

const AddEditRecipeScreen = ({ route, navigation }) => {
  const { recipeId } = route.params || {};
  const isEditing = !!recipeId;
  const { colors } = useTheme();

  const [title, setTitle] = useState('');
  const [category, setCategory] = useState('');
  const [imageUri, setImageUri] = useState('');
  const [ingredients, setIngredients] = useState('');
  const [instructions, setInstructions] = useState('');
  const [notes, setNotes] = useState('');
  const [showNewCategory, setShowNewCategory] = useState(false);
  const [newCategoryName, setNewCategoryName] = useState('');

  const [categories, setCategories] = useState([]);

  useEffect(() => {
    getRecipes().then((list) => {
      const set = new Set();
      list.forEach((r) => r.category && set.add(r.category));
      setCategories(Array.from(set).sort());
    });
  }, []);

  useEffect(() => {
    if (isEditing && recipeId) {
      getRecipeById(recipeId).then((recipe) => {
        if (recipe) {
          setTitle(recipe.title || '');
          setCategory(recipe.category || '');
          setImageUri(recipe.imageUri || '');
          setIngredients(recipe.ingredients || '');
          setInstructions(recipe.instructions || '');
          setNotes(recipe.notes || '');
        }
      });
    }
  }, [isEditing, recipeId]);

  const handlePickImage = async () => {
    const uri = await pickImage();
    if (uri) setImageUri(uri);
  };

  const handleSave = async () => {
    if (!title.trim()) {
      Alert.alert('שגיאה', 'שם המתכון הוא שדה חובה');
      return;
    }
    const finalCategory = (showNewCategory ? newCategoryName : category).trim();

    if (isEditing) {
      const success = await updateRecipe(recipeId, {
        title: title.trim(),
        category: finalCategory,
        imageUri: imageUri || null,
        ingredients: ingredients.trim(),
        instructions: instructions.trim(),
        notes: notes.trim(),
      });
      if (success) {
        Alert.alert('הצלחה', 'המתכון עודכן', [
          { text: 'אישור', onPress: () => navigation.goBack() },
        ]);
      } else {
        Alert.alert('שגיאה', 'שגיאה בעדכון המתכון');
      }
    } else {
      const recipe = {
        id: uuidv4(),
        title: title.trim(),
        category: finalCategory,
        imageUri: imageUri || null,
        ingredients: ingredients.trim(),
        instructions: instructions.trim(),
        notes: notes.trim(),
        isFavorite: false,
        createdAt: Date.now(),
      };
      const success = await addRecipe(recipe);
      if (success) {
        Alert.alert('הצלחה', 'המתכון נשמר', [
          { text: 'אישור', onPress: () => navigation.goBack() },
        ]);
      } else {
        Alert.alert('שגיאה', 'שגיאה בשמירת המתכון');
      }
    }
  };

  const selectedForChips = category || null;

  return (
    <KeyboardAvoidingView
      style={[styles.container, { backgroundColor: colors.background }]}
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
    >
      <Header
        title={isEditing ? 'עריכת מתכון' : 'הוסף מתכון חדש'}
        onBack={() => navigation.goBack()}
      />
      <ScrollView
        style={styles.scroll}
        contentContainerStyle={styles.scrollContent}
        keyboardShouldPersistTaps="handled"
      >
        <View style={styles.form}>
          <CustomInput
            label="שם מתכון"
            value={title}
            onChangeText={setTitle}
            placeholder="הכנס שם מתכון"
            required
          />

          <Text style={[styles.label, { color: colors.text }]}>קטגוריה</Text>
          <CategoryChips
            categories={categories}
            selectedCategory={selectedForChips}
            onSelectCategory={(cat) => {
              setCategory(cat);
              setShowNewCategory(false);
            }}
            newCategoryLabel="+ קטגוריה חדשה"
            onAddNew={() => {
              setShowNewCategory(true);
              setCategory('');
            }}
          />
          {showNewCategory && (
            <CustomInput
              value={newCategoryName}
              onChangeText={setNewCategoryName}
              placeholder="שם קטגוריה חדשה"
            />
          )}

          <Text style={[styles.label, { color: colors.text }]}>תמונה</Text>
          <TouchableOpacity
            style={[styles.imageBox, { backgroundColor: colors.surface, borderColor: colors.border }]}
            onPress={handlePickImage}
          >
            {imageUri ? (
              <Image source={{ uri: imageUri }} style={styles.previewImage} resizeMode="cover" />
            ) : (
              <Text style={[styles.imagePlaceholder, { color: colors.textMuted }]}>
                לחץ לבחירת תמונה
              </Text>
            )}
          </TouchableOpacity>

          <CustomInput
            label="מצרכים"
            value={ingredients}
            onChangeText={setIngredients}
            placeholder="כל מצרך בשורה נפרדת"
            multiline
            numberOfLines={6}
          />
          <CustomInput
            label="אופן הכנה"
            value={instructions}
            onChangeText={setInstructions}
            placeholder="הוראות הכנה"
            multiline
            numberOfLines={8}
          />
          <CustomInput
            label="הערות"
            value={notes}
            onChangeText={setNotes}
            placeholder="הערות (אופציונלי)"
            multiline
            numberOfLines={4}
          />

          <TouchableOpacity
            style={[styles.saveBtn, { backgroundColor: colors.primary }]}
            onPress={handleSave}
          >
            <Text style={styles.saveBtnText}>שמור</Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={[styles.cancelBtn, { borderColor: colors.border }]}
            onPress={() => navigation.goBack()}
          >
            <Text style={[styles.cancelBtnText, { color: colors.text }]}>ביטול</Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </KeyboardAvoidingView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scroll: {
    flex: 1,
  },
  scrollContent: {
    paddingBottom: 40,
  },
  form: {
    padding: 20,
  },
  label: {
    fontSize: 16,
    fontWeight: '600',
    marginBottom: 8,
    textAlign: 'right',
  },
  imageBox: {
    height: 180,
    borderRadius: 16,
    borderWidth: 1,
    borderStyle: 'dashed',
    marginBottom: 20,
    overflow: 'hidden',
    justifyContent: 'center',
    alignItems: 'center',
  },
  previewImage: {
    width: '100%',
    height: '100%',
  },
  imagePlaceholder: {
    fontSize: 16,
  },
  saveBtn: {
    borderRadius: 12,
    paddingVertical: 16,
    marginTop: 8,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 4,
  },
  saveBtnText: {
    fontSize: 18,
    fontWeight: 'bold',
    color: '#FFFFFF',
    textAlign: 'center',
  },
  cancelBtn: {
    borderRadius: 12,
    paddingVertical: 14,
    marginTop: 12,
    borderWidth: 1,
  },
  cancelBtnText: {
    fontSize: 16,
    fontWeight: '600',
    textAlign: 'center',
  },
});

export default AddEditRecipeScreen;
