import AsyncStorage from '@react-native-async-storage/async-storage';

const RECIPES_KEY = '@recipes';

/**
 * Get all recipes from storage
 * @returns {Promise<Array>} Array of recipe objects
 */
export const getRecipes = async () => {
  try {
    const jsonValue = await AsyncStorage.getItem(RECIPES_KEY);
    const recipes = jsonValue != null ? JSON.parse(jsonValue) : [];
    // Normalize: ensure isFavorite exists for legacy data
    return recipes.map((r) => ({
      ...r,
      isFavorite: r.isFavorite === true,
      imageUri: r.imageUri || null,
    }));
  } catch (e) {
    console.error('Error reading recipes:', e);
    return [];
  }
};

/**
 * Save entire recipes array (internal use)
 * @param {Array} recipes
 * @returns {Promise<boolean>}
 */
export const saveRecipes = async (recipes) => {
  try {
    await AsyncStorage.setItem(RECIPES_KEY, JSON.stringify(recipes));
    return true;
  } catch (e) {
    console.error('Error saving recipes:', e);
    return false;
  }
};

/**
 * Add a new recipe
 * @param {Object} recipe - Recipe object (id, title, category, ingredients, instructions, notes, imageUri, isFavorite, createdAt)
 * @returns {Promise<boolean>}
 */
export const addRecipe = async (recipe) => {
  try {
    const recipes = await getRecipes();
    recipes.push({
      ...recipe,
      isFavorite: recipe.isFavorite === true,
    });
    return await saveRecipes(recipes);
  } catch (e) {
    console.error('Error adding recipe:', e);
    return false;
  }
};

/**
 * Update an existing recipe by id
 * @param {string} id
 * @param {Object} updates - Partial recipe object
 * @returns {Promise<boolean>}
 */
export const updateRecipe = async (id, updates) => {
  try {
    const recipes = await getRecipes();
    const index = recipes.findIndex((r) => r.id === id);
    if (index === -1) return false;
    recipes[index] = { ...recipes[index], ...updates, id };
    return await saveRecipes(recipes);
  } catch (e) {
    console.error('Error updating recipe:', e);
    return false;
  }
};

/**
 * Delete a recipe by id
 * @param {string} id
 * @returns {Promise<boolean>}
 */
export const deleteRecipe = async (id) => {
  try {
    const recipes = await getRecipes();
    const filtered = recipes.filter((r) => r.id !== id);
    return await saveRecipes(filtered);
  } catch (e) {
    console.error('Error deleting recipe:', e);
    return false;
  }
};

/**
 * Toggle favorite status for a recipe
 * @param {string} id
 * @returns {Promise<boolean>} New favorite state
 */
export const toggleFavorite = async (id) => {
  try {
    const recipes = await getRecipes();
    const index = recipes.findIndex((r) => r.id === id);
    if (index === -1) return false;
    recipes[index].isFavorite = !recipes[index].isFavorite;
    await saveRecipes(recipes);
    return recipes[index].isFavorite;
  } catch (e) {
    console.error('Error toggling favorite:', e);
    return false;
  }
};

/**
 * Get a single recipe by id
 * @param {string} id
 * @returns {Promise<Object|null>}
 */
export const getRecipeById = async (id) => {
  const recipes = await getRecipes();
  const recipe = recipes.find((r) => r.id === id) || null;
  if (recipe) {
    recipe.isFavorite = recipe.isFavorite === true;
    recipe.imageUri = recipe.imageUri || null;
  }
  return recipe;
};
