/**
 * Web-only export: trigger download via Blob + <a download>.
 * Native version uses expo-file-system + expo-sharing (exportJson.js).
 * @param {Object|Array} data
 * @param {string} filename
 * @returns {Promise<boolean>}
 */
export async function exportJson(data, filename) {
  const jsonContent = JSON.stringify(data, null, 2);
  const sanitized = filename.replace(/[^a-zA-Z0-9\u0590-\u05FF\s.-]/g, '').replace(/\s+/g, '-');
  const name = sanitized.endsWith('.json') ? sanitized : `${sanitized}.json`;
  try {
    const blob = new Blob([jsonContent], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = name;
    a.click();
    URL.revokeObjectURL(url);
    return true;
  } catch (e) {
    console.error('exportJson web:', e);
    return false;
  }
}
