import React from 'react';
import { View, Text, TouchableOpacity, StyleSheet, Platform } from 'react-native';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useTheme } from '../theme/ThemeContext';

/**
 * Reusable header with optional back button and right-side action (e.g. theme toggle).
 */
const Header = ({
  title,
  onBack,
  rightComponent,
  backgroundColor,
  textColor = '#FFFFFF',
}) => {
  const insets = useSafeAreaInsets();
  const { colors } = useTheme();
  const bg = backgroundColor ?? colors.primary;

  return (
    <View
      style={[
        styles.header,
        {
          backgroundColor: bg,
          paddingTop: Platform.OS === 'ios' ? insets.top : 50,
        },
      ]}
    >
      <View style={styles.row}>
        {onBack ? (
          <TouchableOpacity style={styles.backBtn} onPress={onBack}>
            <Text style={[styles.backText, { color: textColor }]}>← חזרה</Text>
          </TouchableOpacity>
        ) : (
          <View style={styles.backBtn} />
        )}
        <Text style={[styles.title, { color: textColor }]} numberOfLines={1}>
          {title}
        </Text>
        {rightComponent ? (
          <View style={styles.right}>{rightComponent}</View>
        ) : (
          <View style={styles.right} />
        )}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  header: {
    paddingBottom: 16,
    paddingHorizontal: 20,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
  row: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  backBtn: {
    minWidth: 80,
    alignItems: 'flex-end',
  },
  backText: {
    fontSize: 16,
    fontWeight: '600',
  },
  title: {
    flex: 1,
    fontSize: 24,
    fontWeight: 'bold',
    textAlign: 'center',
  },
  right: {
    minWidth: 80,
    alignItems: 'flex-start',
  },
});

export default Header;
