import React from 'react';
import { TouchableOpacity, Text, StyleSheet } from 'react-native';
import { useTheme } from '../theme/ThemeContext';

/**
 * Heart icon button for toggling favorite state.
 */
const FavoriteButton = ({ isFavorite, onPress, size = 28 }) => {
  const { colors } = useTheme();
  const heart = isFavorite ? '♥' : '♡';

  return (
    <TouchableOpacity
      onPress={onPress}
      style={[styles.wrap, { width: size + 16, height: size + 16 }]}
      hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
    >
      <Text
        style={[
          styles.heart,
          { fontSize: size, color: isFavorite ? colors.favorite : colors.textMuted },
        ]}
      >
        {heart}
      </Text>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  wrap: {
    justifyContent: 'center',
    alignItems: 'center',
  },
  heart: {
    fontWeight: 'bold',
  },
});

export default FavoriteButton;
